"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { db } from "../../firebaseConfig";
import { doc, deleteDoc } from "firebase/firestore";

type BoardImage = { name: string; src: string };
type PracticeRecord = {
  lessonId: string;
  lessonTitle: string;
  practiceDate: string;
  reflection: string;
  boardImages: BoardImage[];
};
type LessonPlan = {
  id: string;
  result: any; // object expected for detailed display
};

export default function HistoryPage() {
  const [records, setRecords] = useState<PracticeRecord[]>([]);
  const [lessonPlans, setLessonPlans] = useState<LessonPlan[]>([]);
  const [sortKey, setSortKey] = useState<"practiceDate" | "lessonTitle">(
    "practiceDate"
  );
  const router = useRouter();

  useEffect(() => {
    const recs = localStorage.getItem("practiceRecords");
    if (recs) {
      try {
        setRecords(JSON.parse(recs));
      } catch {
        setRecords([]);
      }
    }
    const plans = localStorage.getItem("lessonPlans");
    if (plans) {
      try {
        setLessonPlans(JSON.parse(plans));
      } catch {
        setLessonPlans([]);
      }
    }
  }, []);

  const sorted = [...records].sort((a, b) => {
    if (sortKey === "lessonTitle") return a.lessonTitle.localeCompare(b.lessonTitle);
    return a.practiceDate.localeCompare(b.practiceDate);
  });

  const handleDelete = async (lessonId: string) => {
    if (!confirm("„Åì„ÅÆÂÆüË∑µË®òÈå≤„ÇíÂâäÈô§„Åó„Åæ„Åô„ÅãÔºü")) return;
    try {
      await deleteDoc(doc(db, "practice_records", lessonId));
    } catch {
      alert("Firestore ‰∏ä„ÅÆÂâäÈô§„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
      return;
    }
    const next = records.filter((r) => r.lessonId !== lessonId);
    setRecords(next);
    localStorage.setItem("practiceRecords", JSON.stringify(next));
  };

  const handleExportRecordPdf = async (lessonId: string) => {
    const { default: html2pdf } = await import("html2pdf.js");
    const el = document.getElementById(`record-${lessonId}`);
    if (!el) return alert("PDFÂåñÁî®„ÅÆË¶ÅÁ¥†„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì„Åß„Åó„Åü„ÄÇ");

    await Promise.all(
      Array.from(el.querySelectorAll("img")).map(
        (img) =>
          new Promise((resolve) => {
            if (img.complete) resolve(null);
            else {
              img.onload = () => resolve(null);
              img.onerror = () => resolve(null);
            }
          })
      )
    );

    await html2pdf()
      .from(el)
      .set({
        margin: 5,
        filename: `${sorted.find(r => r.lessonId === lessonId)?.lessonTitle || lessonId}_ÂÆüË∑µË®òÈå≤.pdf`,
        jsPDF: { unit: "mm", format: "a4", orientation: "portrait" },
        html2canvas: { useCORS: true, scale: 2 },
        pagebreak: { mode: ["avoid-all"] },
      })
      .save();
  };

  const handleDriveSave = async (lessonId: string) => {
    const { default: html2pdf } = await import("html2pdf.js");
    const el = document.getElementById(`record-${lessonId}`);
    if (!el) return alert("Drive‰øùÂ≠òÁî®„ÅÆË¶ÅÁ¥†„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì„Åß„Åó„Åü„ÄÇ");

    await Promise.all(
      Array.from(el.querySelectorAll("img")).map(
        (img) =>
          new Promise((resolve) => {
            if (img.complete) resolve(null);
            else {
              img.onload = () => resolve(null);
              img.onerror = () => resolve(null);
            }
          })
      )
    );

    const pdfBlob: Blob = await html2pdf()
      .from(el)
      .set({
        margin: 5,
        jsPDF: { unit: "mm", format: "a4", orientation: "portrait" },
        html2canvas: { useCORS: true, scale: 2 },
        pagebreak: { mode: ["avoid-all"] },
      })
      .outputPdf("blob");

    const { uploadToDrive } = await import("../../../lib/drive");
    const folderId = process.env.NEXT_PUBLIC_GOOGLE_DRIVE_FOLDER_ID;
    if (!folderId) return alert("Drive„Éï„Ç©„É´„ÉÄID„ÅåÊú™Ë®≠ÂÆö„Åß„Åô„ÄÇ");

    try {
      await uploadToDrive(
        pdfBlob,
        `${sorted.find(r => r.lessonId === lessonId)?.lessonTitle || lessonId}_ÂÆüË∑µË®òÈå≤.pdf`,
        "application/pdf",
        folderId
      );
      alert("Drive„Å∏„ÅÆ‰øùÂ≠ò„ÅåÂÆå‰∫Ü„Åó„Åæ„Åó„Åü„ÄÇ");
    } catch {
      alert("Drive‰øùÂ≠ò„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
    }
  };

  const navLinkStyle: React.CSSProperties = {
    padding: "8px 12px",
    backgroundColor: "#1976d2",
    color: "white",
    border: "none",
    borderRadius: 6,
    fontSize: "1rem",
    textDecoration: "none",
    cursor: "pointer",
    whiteSpace: "nowrap",
  };
  const cardStyle: React.CSSProperties = {
    display: "flex",
    flexDirection: "row",
    alignItems: "flex-start",
    justifyContent: "space-between",
    backgroundColor: "#fdfdfd",
    border: "2px solid #ddd",
    borderRadius: 12,
    padding: 16,
    marginBottom: 16,
    boxShadow: "0 2px 6px rgba(0,0,0,0.05)",
  };
  const actionBtn: React.CSSProperties = {
    padding: "8px 12px",
    backgroundColor: "#4CAF50",
    color: "white",
    border: "none",
    borderRadius: 6,
    fontSize: "0.9rem",
    cursor: "pointer",
    width: "100%",
  };
  const deleteBtn: React.CSSProperties = { ...actionBtn, backgroundColor: "#f44336" };
  const pdfBtn: React.CSSProperties = { ...actionBtn, backgroundColor: "#607D8B" };
  const planBlockStyle: React.CSSProperties = {
    backgroundColor: "#fafafa",
    border: "1px solid #ccc",
    borderRadius: 6,
    padding: 12,
    marginTop: 12,
    whiteSpace: "normal",
    fontFamily: "sans-serif",
    fontSize: "0.9rem",
  };

  return (
    <main style={{ padding: 24, fontFamily: "sans-serif", maxWidth: 960, margin: "0 auto" }}>
      <nav
        style={{
          display: "flex",
          gap: 12,
          overflowX: "auto",
          marginBottom: 24,
          justifyContent: "center",
        }}
      >
        <Link href="/" style={navLinkStyle}>
          üè† „Éõ„Éº„É†
        </Link>
        <Link href="/plan" style={navLinkStyle}>
          üìã ÊéàÊ•≠‰ΩúÊàê
        </Link>
        <Link href="/plan/history" style={navLinkStyle}>
          üìñ Ë®àÁîªÂ±•Ê≠¥
        </Link>
        <Link href="/practice/history" style={navLinkStyle}>
          üì∑ ÂÆüË∑µÂ±•Ê≠¥
        </Link>
        <Link href="/models/create" style={navLinkStyle}>
          ‚úèÔ∏è ÊïôËÇ≤Ë¶≥‰ΩúÊàê
        </Link>
        <Link href="/models" style={navLinkStyle}>
          üìö ÊïôËÇ≤Ë¶≥‰∏ÄË¶ß
        </Link>
        <Link href="/models/history" style={navLinkStyle}>
          üïí ÊïôËÇ≤Ë¶≥Â±•Ê≠¥
        </Link>
      </nav>

      <h2 style={{ fontSize: "1.8rem", marginBottom: 16 }}>ÂÆüË∑µË®òÈå≤‰∏ÄË¶ß</h2>

      <label style={{ display: "block", textAlign: "right", marginBottom: 16 }}>
        ‰∏¶„Å≥Êõø„ÅàÔºö
        <select
          value={sortKey}
          onChange={(e) => setSortKey(e.target.value as any)}
          style={{ marginLeft: 8, padding: 6, fontSize: "1rem" }}
        >
          <option value="practiceDate">ÂÆüÊñΩÊó•È†Ü</option>
          <option value="lessonTitle">„Çø„Ç§„Éà„É´È†Ü</option>
        </select>
      </label>

      {sorted.length === 0 ? (
        <p style={{ textAlign: "center", fontSize: "1.2rem" }}>„Åæ„Å†ÂÆüË∑µË®òÈå≤„Åå„ÅÇ„Çä„Åæ„Åõ„Çì„ÄÇ</p>
      ) : (
        <div style={{ display: "flex", flexDirection: "column" }}>
          {sorted.map((r, idx) => {
            const plan = lessonPlans.find((p) => p.id === r.lessonId);
            return (
              <article key={`${r.lessonId}-${idx}`} style={cardStyle}>
                <div id={`record-${r.lessonId}`} style={{ flex: 1 }}>
                  <h3 style={{ margin: "0 0 8px" }}>{r.lessonTitle}</h3>

                  {plan && typeof plan.result === "object" && (
                    <div style={planBlockStyle}>
                      <strong>ÊéàÊ•≠Ê°à</strong>
                      <div>
                        <p><strong>ÊïôÁßëÊõ∏ÂêçÔºö</strong>{plan.result["ÊïôÁßëÊõ∏Âêç"] || "Ôºç"}</p>
                        <p><strong>ÂçòÂÖÉÂêçÔºö</strong>{plan.result["ÂçòÂÖÉÂêç"] || "Ôºç"}</p>
                        <p><strong>ÊéàÊ•≠ÊôÇÈñìÊï∞Ôºö</strong>{plan.result["ÊéàÊ•≠ÊôÇÈñìÊï∞"] || "Ôºç"}ÊôÇÈñì</p>
                        <p><strong>ÂçòÂÖÉ„ÅÆÁõÆÊ®ôÔºö</strong>{plan.result["ÂçòÂÖÉ„ÅÆÁõÆÊ®ô"] || "Ôºç"}</p>

                        {plan.result["Ë©ï‰æ°„ÅÆË¶≥ÁÇπ"] && (
                          <div style={{ marginTop: 8 }}>
                            <strong>Ë©ï‰æ°„ÅÆË¶≥ÁÇπÔºö</strong>
                            <ul style={{ marginTop: 4, paddingLeft: 16 }}>
                              {Object.entries(plan.result["Ë©ï‰æ°„ÅÆË¶≥ÁÇπ"]).map(([key, values]) => (
                                <li key={key}>
                                  <strong>{key}:</strong>{" "}
                                  {Array.isArray(values) ? values.join("„ÄÅ") : String(values)}
                                </li>
                              ))}
                            </ul>
                          </div>
                        )}

                        <p><strong>ËÇ≤„Å¶„Åü„ÅÑÂ≠ê„Å©„ÇÇ„ÅÆÂßøÔºö</strong>{plan.result["ËÇ≤„Å¶„Åü„ÅÑÂ≠ê„Å©„ÇÇ„ÅÆÂßø"] || "Ôºç"}</p>

                        <p><strong>Ë®ÄË™ûÊ¥ªÂãï„ÅÆÂ∑•Â§´Ôºö</strong>{plan.result["Ë®ÄË™ûÊ¥ªÂãï„ÅÆÂ∑•Â§´"] || "Ôºç"}</p>

                        {plan.result["ÊéàÊ•≠„ÅÆÊµÅ„Çå"] && (
                          <div style={{ marginTop: 8 }}>
                            <strong>ÊéàÊ•≠„ÅÆÊµÅ„ÇåÔºö</strong>
                            <ul style={{ marginTop: 4, paddingLeft: 16 }}>
                              {Object.entries(plan.result["ÊéàÊ•≠„ÅÆÊµÅ„Çå"]).map(([key, val]) => {
                                const content = typeof val === "string" ? val : JSON.stringify(val);
                                return (
                                  <li key={key}>
                                    <strong>{key}:</strong> {content}
                                  </li>
                                );
                              })}
                            </ul>
                          </div>
                        )}
                      </div>
                    </div>
                  )}

                  <p style={{ marginTop: 16 }}>
                    <strong>ÂÆüÊñΩÊó•Ôºö</strong> {r.practiceDate}
                  </p>

                  <p>
                    <strong>ÊåØ„ÇäËøî„ÇäÔºö</strong>
                    <br />
                    {r.reflection}
                  </p>

                  {r.boardImages.length > 0 && (
                    <div style={{ marginTop: 8, display: "flex", flexDirection: "column", gap: 12 }}>
                      {r.boardImages.map((img, i) => (
                        <div key={`${img.name}-${i}`} style={{ width: "100%" }}>
                          <div style={{ marginBottom: 6, fontWeight: "bold" }}>ÊùøÊõ∏{i + 1}</div>
                          <img
                            src={img.src}
                            alt={img.name}
                            style={{
                              width: "100%",
                              height: "auto",
                              borderRadius: 8,
                              border: "1px solid #ccc",
                              display: "block",
                              maxWidth: "100%",
                              objectFit: "contain",
                            }}
                          />
                        </div>
                      ))}
                    </div>
                  )}
                </div>

                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    gap: 8,
                    marginLeft: 16,
                    width: 120,
                  }}
                >
                  <button onClick={() => handleExportRecordPdf(r.lessonId)} style={pdfBtn}>
                    üìÑ PDFÂá∫Âäõ
                  </button>
                  <button onClick={() => handleDriveSave(r.lessonId)} style={pdfBtn}>
                    ‚òÅÔ∏è Drive‰øùÂ≠ò
                  </button>
                  <Link href={`/practice/add/${r.lessonId}`}>
                    <button style={actionBtn}>‚úèÔ∏è Á∑®ÈõÜ</button>
                  </Link>
                  <button onClick={() => handleDelete(r.lessonId)} style={deleteBtn}>
                    üóë ÂâäÈô§
                  </button>
                </div>
              </article>
            );
          })}
        </div>
      )}
    </main>
  );
}
